.languageSwitcher{display:flex;align-items:center;gap:.5rem;font-size:.9rem}.langButton{background:none;border:none;color:#5a6c7d;cursor:pointer;padding:.25rem .5rem;border-radius:.25rem;transition:all .2s ease;font-size:.9rem;font-weight:400}.langButton:hover{color:#000;background:rgba(0,0,0,.05)}.langButton.active{color:#6c9;font-weight:500}.separator{color:#d1d5db;font-weight:300}